import { createFeatureSelector, createSelector } from '@ngrx/store';
import { IRecordDesignerModel, IRecordDesignerState } from './record-designer.types';
import { RX_RECORD_DESIGNER } from '../record-designer.constant';

export const recordDesignerStateSelector = createFeatureSelector<IRecordDesignerState>(
  RX_RECORD_DESIGNER.featureSelector
);

export const recordDesignerModelSelector = createSelector(
  recordDesignerStateSelector,
  (recordDesignerState: IRecordDesignerState) => recordDesignerState.model
);

export const definitionNameSelector = createSelector(
  recordDesignerModelSelector,
  (recordDesignerModel: IRecordDesignerModel) => recordDesignerModel.definitionName
);

export const bundleIdSelector = createSelector(
  recordDesignerModelSelector,
  (recordDesignerModel: IRecordDesignerModel) => recordDesignerModel.bundleId
);

export const isDesignModeSelector = createSelector(
  recordDesignerModelSelector,
  (recordDesignerModel: IRecordDesignerModel) => recordDesignerModel.isDesignMode
);

export const isDirtySelector = createSelector(
  recordDesignerModelSelector,
  (recordDesignerModel: IRecordDesignerModel) => recordDesignerModel.isDirty
);

export const inspectorTabIndexSelector = createSelector(
  recordDesignerModelSelector,
  (recordDesignerModel: IRecordDesignerModel) => recordDesignerModel.inspectorTabIndex
);

export const definitionModelSelector = createSelector(
  recordDesignerModelSelector,
  (recordDesignerModel: IRecordDesignerModel) => recordDesignerModel.definitionModel
);

export const definitionModelFromDefinitionSelector = createSelector(
  recordDesignerModelSelector,
  (recordDesignerModel: IRecordDesignerModel) => recordDesignerModel.definitionModelFromDefinition
);

export const selectedFieldGuidSelector = createSelector(
  recordDesignerModelSelector,
  (recordDesignerModel: IRecordDesignerModel) => recordDesignerModel.selectedFieldGuid
);

export const savedDefinitionNameSelector = createSelector(
  recordDesignerModelSelector,
  (recordDesignerModel: IRecordDesignerModel) => recordDesignerModel.savedDefinitionName
);
